import * as modules from '../modules';
import meta from '../assets/meta/metaData';
import Snap from 'snapsvg-cjs';

const GLOB = {};

const createLoader = g => g.text(400, 200, '로딩중!!!').attr({ textAnchor: 'middle' });

const preloadImages = async code => {
    const images = await import(`../assets/images/${code}`);

    if (!GLOB[code]) {
        GLOB[code] = { images: [] };

        for (const key in images) {
            if (Object.hasOwnProperty.call(images, key)) {
                const url = images[key];
                const defsG = GLOB.SVG.g();
                const image = defsG.image(url, 0, 0).toDefs();

                GLOB[code].images.push({
                    key,
                    image
                });
            }
        }
    }
};

const start = async code => {
    const SVG = Snap('#frame2d');
    GLOB.SVG = SVG;

    GLOB.SVG.clear();
    const loader = createLoader(GLOB.SVG);
    await preloadImages(code);
    await new Promise(resolve => setTimeout(resolve, 1000));
    loader.remove();

    modules[code]({ canvas: GLOB.SVG, ...meta[code] });
};

export const contentsList = [
    { code: 'C0001', title: '자동차' },
    { code: 'C0002', title: '애니팡' }
];
export default start;
